import MvCountDown12 from '../comjishi/jishi12.vue';
import MvCountDown15 from '../comjishi/jishi15.vue';
import MvCountDown from '../comjishi/jishi30.vue';


export default {
	props: {
		item: Object,
		voSocketPay: Boolean
	},
	components: {
		MvCountDown,
		MvCountDown12,
		MvCountDown15

	},
	data() {
		return {

			tableData: [],
			centerDialogVisible: false, //二维码弹窗
			fullscreenLoading: false,
			//order:'',
			myInterval: null, //订单状态轮询
			myIntervalPay: null, //支付结果轮询
			voSocket: false,

			isLunxun: false,
			codes: ''
		}
	},
	
	methods: {
	
		
		checke_isButtenz() {
			this.$emit("getordlist")
		},
		
		//支付
		      payOrder(id) {
		        if (this.fullscreenLoading) {
		        	uni.showModal({
		        	    title: '提示',
		        	    content: '订单生成中请稍后',
		        	});
		        	this.fullscreenLoading=false;
		        	return false;
		        }
		        native_pay_order(id).then(res => {
		          this.fullscreenLoading = false;
		          if (res.status === 0) {
		            this.useqrcode(res.msg, id)
		          } else {
		            
		          }
		        });
		      },
		
		//操作
		operationRow(scope, type) {
			if (this.fullscreenLoading) {
				uni.showModal({
				    title: '提示',
				    content: '上一个操作还没有响应，请稍后再试',
				});
				this.fullscreenLoading=false;
				return false;
			}
			this.fullscreenLoading = true;
			let order = {
				id: scope.voOrder.id,
				type: type
			};
			//操作
			this.$http.post(this.$urlconfig.getoscaozuo, order, {}).then(data => {
				this.fullscreenLoading = false;
				if (data.status === 0) {
					uni.showToast({
						title: '操作成功',
						icon: "none"
					});
					this.checke_isButtenz();
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					});
				}
			});

			//11--》3关单  或者 13 抢单确认-->12 支付按键-->支付成功变成4 （送货者操作4 变成16） --> 16 确认

			// -->状态==5  （去评价）   status==3|| 17   传给后端 11再次开启
		},

		//选择此家
		choice(scope, props, type) {
			if (this.fullscreenLoading) {
				uni.showToast({
					title: '操作中请稍后',
					icon: "none"
				});
			}
			this.fullscreenLoading = true;
			let order = {};
			order.id = props.voOrder.id;
			order.type = type;
			order.orderCommonOfferId = scope.orderCommonOffer.id;
			order.commodityZongJiage = scope.orderCommonOffer.commodityZongJiage;
			//查询有没有待支付订单
			this.$http.post(this.$urlconfig.getoscaozuo, order, {}).then(data => {
				this.fullscreenLoading = false;
				if (data.status === 0) {
					uni.showToast({
						title: '操作成功',
						icon: "none"
					});
					this.checke_isButtenz();
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					});
				}
			});
		},
		//倒计时相关开始
		headercell(row) {

			if (row.orderStatu11 === true) {
				//获取创建时间
				let newDateGetTime = new Date().getTime();
				let date2 = new Date(row.voOrder.createTime);
				//获取时间差 毫秒，getTime()获取毫秒值
				let second = newDateGetTime - date2.getTime();
				if (second < 1800000) {
					return newDateGetTime + 1800000 - second;
				} else {
					return 0;
				}
			}
		},
		//超过30分钟有报价，给15分钟buffer
		headercel8(row) {
			if (row.orderStatu18 === true) {
				//获取创建时间
				let newDateGetTime = new Date().getTime();
				let date2 = new Date(row.voOrder.createTime);
				//获取时间差 毫秒，getTime()获取毫秒值
				let second = newDateGetTime - date2.getTime();
				if (second > 1800000 && second <= 45 * 60 * 1000) {
					return newDateGetTime + 45 * 60 * 1000 - second;
				} else {
					return 0;
				}
			}
		},
		//待销售支付定金15分钟
		headercel3(row, num) {
			if (row.orderStatu13 === true || row.orderStatu12 === true) {
				//获取创建时间
				let newDateGetTime = new Date().getTime();
				let date2 = new Date(row.voOrder.updateTime);
				//获取时间差 毫秒，getTime()获取毫秒值
				let second = newDateGetTime - date2.getTime();
				if (second < num * 60 * 1000) {
					return newDateGetTime + num * 60 * 1000 - second;
				} else {
					return 0;
				}
			}
		},


		countDownS(x) {
			// 开始倒计时回调
			// console.log(x)
		},
		countDownE() {
			// 结束倒计时回调
			this.checke_isButtenz();
		}
		//倒计时相关结束

	}
}
